'use strict';
/**
 * @api SET_TITLE(title,fn) SET_TITLE
 * @apiGroup APP
 * @apiVersion 1.0.0
 * @apiParam {string} title 浏览器标题
 * @apiParam {function} fn 点击回调事件
 * @apiDescription 设置浏览器标题及其点击事件
 * @apiUse APPNpm
 * @apiUse APPJs
 * @apiParamExample {string} 例子
 * SET_TITLE('注册页',_=>{
 *    scrollTo(0,0);
 * })
 * // 标题设置为 注册页
 * // 点击标题后返回页面顶部
 * @apiParamExample {string} 返回值
 * 无
 **/
import {WinJSDec} from './APP/config';

const name = 'SET_TITLE';
const SET_TITLE = (title, fn = _ => {
}) => {
  const rds = new Date() * 1;
  const random = ('___callback___' + rds);
  window[random] = _ => {
    fn && fn();
  };
  document.title = title;
  WinJSDec(_ => {
    window.WinJSBridge.call('webview', 'setTitle', {
      title,
      onclick: random + '()'
    });
  });
};
export default SET_TITLE;
